@import '../../styles/utils/mixins';

.container {
  background-color: var(--color-semi-background);
  border-radius: 8px;
  padding: 24px;
  padding-bottom: 48px;

  max-width: 382px;

  transition: all 0.3s linear;

  &:hover {
    transform: translateY(-5%);
  }

  &MemeMode {
    background: linear-gradient(
        rgba(255, 0, 0, 1) 0%,
        rgba(255, 154, 0, 1) 10%,
        rgba(208, 222, 33, 1) 20%,
        rgba(79, 220, 74, 1) 30%,
        rgba(63, 218, 216, 1) 40%,
        rgba(47, 201, 226, 1) 50%,
        rgba(28, 127, 238, 1) 60%,
        rgba(95, 21, 242, 1) 70%,
        rgba(186, 12, 248, 1) 80%,
        rgba(251, 7, 217, 1) 90%,
        rgba(255, 0, 0, 1) 100%
      )
      0 0/100% 200%;
    animation: a 2s linear infinite;
  }
}

@keyframes a {
  to {
    background-position: 0 -200%;
  }
}

.personInfo {
  display: flex;
  align-items: center;
}

.personTextInfo {
  margin-right: 10px;
}

.personPhotoInfoImg {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;

  &Meme {
    animation: scale 1s infinite;
  }
}

@keyframes scale {
  50% {
    transform: scale(1.5) rotate(180deg);
  }

  100% {
    transform: scale(1) rotate(360deg);
  }
}

.personPhotoInfo {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 16px;

  height: 56px;
  width: 56px;
  border-radius: 50%;

  background-color: #f6f6f7;
  border: 2px solid var(--color-grey);
}

.personName {
  font-weight: 600;
  font-size: 18px;
  line-height: 20px;
}

.personSkill {
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
  margin-right: 30px;
}

.tl {
  color: var(--color-primary);
}

.contacts {
  margin-top: 32px;
}

.header {
  font-weight: 600;
  font-size: 18px;
  line-height: 15px;
}

.links {
  margin-top: 12px;

  font-weight: 400;
  font-size: 16px;
  line-height: 12px;
}

.link {
  margin-top: 16px;
}

.link:hover {
  color: var(--color-primary);
}

.responsibilities {
  margin-top: 24px;
}

.responsibilitiesText {
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
}

.responsibilitiesList {
  margin-top: 12px;
}

.responsibility {
  font-weight: 400;
  font-size: 16px;
  line-height: 12px;

  &:not(:first-child) {
    margin-top: 8px;
  }
}

@include onTablet {
  .container {
    max-width: 400px;
  }
}
